import React, { useState, useEffect } from "react";
import { Form, Col, Row, Modal } from "react-bootstrap";
import { Button } from "@mui/material";
import axios from "axios";
import { useNavigate } from 'react-router-dom';


function EventsForm() {

  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [showErrorModal, setShowErrorModal] = useState(false);


  const [data, setData] = useState({
    id: " ",
    date: " ",
    event: " ",
    contact: " ",
  });

  const handleSubmit = (event) => {
    event.preventDefault();
    const formdata = new FormData();
    formdata.append("id", data.id);
    formdata.append("date", data.date);
    formdata.append("event", data.event);
    formdata.append("contact", data.contact);

    // Perform form submission logic here

    // Assuming successful form submission
    setShowSuccessModal(true);

    // If submission fails
    // setShowErrorModal(true);


    axios
      .post("http://localhost:8081/addevents", formdata)
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };

  const navigate = useNavigate();

  const handleCloseSuccessModal = () => {
    navigate("/adminevents");
    setShowSuccessModal(false);
  };

  const handleCloseErrorModal = () => {
    setShowErrorModal(false);
  };



  return (
    <Form onSubmit={handleSubmit} className="border p-3">

      <Form.Group as={Row} controlId="id">
        <Form.Label column sm={2} className="fw-bold fs-6">ID :</Form.Label>
        <Col sm={12}>
          <Form.Control
            type="text"
            required
            // value={id}
            onChange={(event) => setData({ ...data, id: event.target.value })}
          />
        </Col>
      </Form.Group>

      <Form.Group as={Row} controlId="date">
        <Form.Label column sm={2} className="fw-bold fs-6">Event_Date:</Form.Label>
        <Col sm={12}>
          <Form.Control
            type="date"
            required
            placeholder="YYYY-MM-DD"
            // value={date}
            onChange={(event) => setData({ ...data, date: event.target.value })}
          />
        </Col>
      </Form.Group>

      <Form.Group as={Row} controlId="eventName">
        <Form.Label column sm={2} className="fw-bold fs-6">Event_Name:</Form.Label>
        <Col sm={12}>
          <Form.Control
            type="text"
            required
            // value={eventName}
            onChange={(event) => setData({ ...data, event: event.target.value })}
          />
        </Col>
      </Form.Group>

      <Form.Group as={Row} controlId="contact">
        <Form.Label column sm={2} className="fw-bold fs-6">Contact_Number:</Form.Label>
        <Col sm={12}>
          <Form.Control
            type="text"
            required
            // value={contact}
            onChange={(event) => setData({ ...data, contact: event.target.value })}
          />
        </Col>
      </Form.Group>

      {/* Success Modal */}
      <Modal
        show={showSuccessModal}
        onHide={handleCloseSuccessModal}
        centered // Added centered prop to show the modal in the center
      >
        <Modal.Header closeButton>
          <Modal.Title>Success</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Form submitted successfully!</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleCloseSuccessModal}>
            OK
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Error Modal */}
      <Modal
        show={showErrorModal}
        onHide={handleCloseErrorModal}
        centered // Added centered prop to show the modal in the center
      >
        <Modal.Header closeButton>
          <Modal.Title>Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Error submitting the form. Please try again.</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseErrorModal}>
            OK
          </Button>
        </Modal.Footer>
      </Modal>

      <Form.Group as={Row} controlId="submit" className="mt-3">
        <Col sm={{ span: 10, offset: 2 }}>
          <Button variant="primary" type="submit">Submit</Button>
        </Col>
      </Form.Group>

    </Form>
  );
}

export default EventsForm;
